import { __ } from '@wordpress/i18n';
import {
  useBlockProps,
  InspectorControls,
  MediaUpload,
  MediaUploadCheck
} from '@wordpress/block-editor';
import { 
  PanelBody, 
  Button,
  CheckboxControl,
  DateTimePicker, 
  SelectControl,
  TextControl, 
  TextareaControl
} from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { dateI18n } from '@wordpress/date';
          
import SimpleTemplate from './templates/simpleTemplate';
import {
  BLOCK_NAMESPACE, EVENT_STATUS_OPTIONS
} from './constants';

export default function edit({ attributes, setAttributes }) {
  const {
    eventName, description,
    imageId,
    startDateTime, endDateTime, dateFormat, 
    organizer, organizerURL, performer,
    venue, address, city,
    offers,
    price, priceCurrency,
    eventStatus, 
    showEventName, showAddress, showDescription, showOrganizer, showPerformer
   } = attributes;

  const blockProps = useBlockProps();

  const formattedStartDate = dateI18n(dateFormat, startDateTime);

  const image = useSelect(
    (select) => imageId ? 
      (select('core')?.getMedia(imageId) || null)
      : null,
    [imageId]
  );

  return (
    <>
      <InspectorControls>
        <PanelBody title={__('Evento', 'ao-events')} initialOpen={true}>
          <TextControl
            label={__('Nombre del evento', 'ao-events')}
            value={eventName}
            onChange={(val) => setAttributes({ eventName: val })}
          />
          <CheckboxControl
            label={__('Mostrar nombre del evento', 'ao-events')}
            checked={!!showEventName}
            onChange={(val) => setAttributes({ showEventName: val })}
          />

          <TextControl
            label={__('Organizador', 'ao-events')}
            value={organizer}
            onChange={(val) => setAttributes({ organizer: val })}
          />
          <TextControl
            label={ __('Web Organizador', 'ao-events') }
            type="url"
            value={ organizerURL }
            onChange={ (value) => setAttributes({ organizerURL: value }) }
          />
          <CheckboxControl
            label={__('Mostrar organizador', 'ao-events')}
            checked={!!showOrganizer}
            onChange={(val) => setAttributes({ showOrganizer: val })}
          />

          <TextControl
            label={__('Intérprete / Artista', 'ao-events')}
            value={performer}
            onChange={(val) => setAttributes({ performer: val })}
          />
          <CheckboxControl
            label={__('Mostrar Intérprete / Artista', 'ao-events')}
            checked={!!showPerformer}
            onChange={(val) => setAttributes({ showPerformer: val })}
          />

          <TextControl
            label={__('Lugar / Venue', 'ao-events')}
            value={venue}
            onChange={(val) => setAttributes({ venue: val })}
          />
          <TextControl
            label={__('Dirección', 'ao-events')}
            value={address}
            onChange={(val) => setAttributes({ address: val })}
          />
          <CheckboxControl
            label={__('Mostrar dirección', 'ao-events')}
            checked={!!showAddress}
            onChange={(val) => setAttributes({ showAddress: val })}
          />          
          <TextControl
            label={__('Ciudad', 'ao-events')}
            value={city}
            onChange={(val) => setAttributes({ city: val })}
          />
          <TextareaControl
            label={__('Descripción', 'ao-events')}
            value={description}
            onChange={(val) => setAttributes({ description: val })}
          />
          <CheckboxControl
            label={__('Mostrar descripción', 'ao-events')}
            checked={!!showDescription}
            onChange={(val) => setAttributes({ showDescription: val })}
          />   
          <MediaUploadCheck>
            <MediaUpload
              onSelect={(media) => {
                setAttributes({ imageId: media.id, imageURL: media.url })}
              }
              allowedTypes={['image']}
              value={imageId}
              render={({ open }) => (
                  <div>
                    {image ? (
                      <>
                        <img
                          src={image.source_url}
                          alt={image.alt_text || ""}
                          style={{ maxWidth: '100%', marginBottom: '8px' }} />
                        <Button
                          onClick={() => setAttributes({ imageId: 0, imageURL: "" })}
                          isDestructive
                          variant='secondary'
                        >
                          {__('Quitar imagen', 'ao-events')}
                        </Button>
                      </>
                    ) : (
                      <Button onClick={open} variant='primary'>
                        {__('Seleccionar imagen', 'ao-events')}
                      </Button>
                    )}
                  </div>
                )
              }
            />
          </MediaUploadCheck>
        </PanelBody>        
        <PanelBody title={`${__('Estado del evento', 'ao-events')} ${eventStatus}`} initialOpen={false}>
          <SelectControl
            label={__('Estado', 'ao-events')}
            value={eventStatus}
            options={EVENT_STATUS_OPTIONS}
            onChange={(value) => setAttributes({ eventStatus: value })}
          />
        </PanelBody>
        <PanelBody title={__('Entradas', 'ao-events')} initialOpen={true}>
          <TextControl
            label={__('Precio', 'ao-events')}
            value={price}
            onChange={(val) => setAttributes({ price: val })}
          />
          <TextControl
            label={__('Moneda', 'ao-events')}
            value={priceCurrency}
            onChange={(val) => setAttributes({ priceCurrency: val })}
          />

          <OffersFields 
            offers={ offers }
            setAttributes={setAttributes}
          />
        </PanelBody>
        <PanelBody title={ `${__('Fechas', 'ao-events')} ${startDateTime ? formattedStartDate : ''}`} initialOpen={false}>
          <SelectControl
            label={__('Formato de fecha', 'ao-events')}
            value={dateFormat}
            options={[
              { label: 'dd-mm-yyyy (16-Ago-2025)', value: 'd-M-Y' },
              { label: 'l j F – H.i\h. (Sábado 16 Agosto - 20.30h.)', value: 'l j F – H.i\\h.' },
              { label: 'hh:mm (12:30)', value: 'H:i' }
            ]}
            onChange={(val) => setAttributes({ dateFormat: val })}
          />
          <DateTimePicker
            label={__('Fecha inicio', 'ao-events')}
            currentDate={ (startDateTime || new Date().toISOString()) }
            is12hour={false}
            onChange={ (newDate) => setAttributes({ startDateTime: newDate }) }
            __nextRemoveHelpButton
          />
          <DateTimePicker
            label={__('Fecha fin', 'ao-events')}
            currentDate={ (endDateTime || new Date().toISOString()) }
            is12hour={false}
            onChange={ (newDate) => setAttributes({ endDateTime: newDate }) }
            __nextRemoveHelpButton
          />
        </PanelBody>
      </InspectorControls>

      <div {...blockProps}>
        { startDateTime ?
            (
              <SimpleTemplate attributes={attributes} baseClass={BLOCK_NAMESPACE} />
            )
            : __('Selecciona fecha y hora…', 'ao-events') 
        }
      </div>
    </>
  );
}

function OffersFields({ offers, setAttributes }) {
  const updateOffer = (value, index) => {
    const newOffers = [...offers];
    newOffers[index] = value;
    setAttributes({ offers: newOffers });
  };

  const addOffer = () => {
    setAttributes({ offers: [...offers, ''] });
  };

  const removeOffer = (index) => {
    const newOffers = offers.filter((_, i) => i !== index);
    setAttributes({ offers: newOffers });
  };

  return (
    <div className="offers-fields">
      {offers.map((offer, index) => (
        <div key={index} className="offers-item">
          <TextControl
            value={offer}
            onChange={(val) => updateOffer(val, index)}
            placeholder="https://ejemplo.com/entradas"
          />
          <Button
            variant="secondary"
            isDestructive
            onClick={() => removeOffer(index)}
          >
            ✕
          </Button>
        </div>
      ))}
      <Button variant="primary" onClick={addOffer}>{__('+ Añadir enlace','ao-events')}</Button>
    </div>
  );
}
